.DrawerToggle {
    width: 4rem;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    padding: 1rem 0;
    box-sizing: border-box;
    cursor: pointer;
}

.DrawerToggle div {
    width: 90%;
    height: .3rem;
    background-color: white;
}

@media (min-width: 50em) {
    .DrawerToggle {
        display: none;
    }
}